module.exports = ".home_container{\r\n  margin-left: 25%;\r\n}\r\n.col-12 col-md-3 aside-content {\r\n    height: 100%;\r\n}\r\nbody{\r\n  padding: 0px;\r\n  margin: 0px;\r\n}\r\n/*Adjust icon size*/\r\ni{\r\n  font-size:1.3em;\r\n  color:black;\r\n}\r\n/*Apply corporate color for the nav bar*/\r\n.bg-color{\r\n  background-color: #555;\r\n}\r\nli{\r\n  margin-right: 20px;\r\n  font-size: 17px;\r\n}\r\nfooter{\r\n  line-height: 80px;\r\n}\r\n.side-image{\r\n  background-image: url(\"/assets/background-image.jpg\");  \r\n  height:500px;\r\n  width:500px;\r\n  \r\n}\r\n/********** HOVER MOUSE FOR ICON BAR *****************/\r\n/* The container <div> - needed to position the dropdown content */\r\n.drop-down {\r\n  position: relative;\r\n  display: inline-block;\r\n}\r\n/* Dropdown Content (Hidden by Default) */\r\n.dropdown-content {\r\n  display: none;\r\n  position: absolute;\r\n  background-color: #f1f1f1;\r\n  min-width: 160px;\r\n  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);\r\n  z-index: 1;\r\n}\r\n/* Links inside the dropdown */\r\n.dropdown-content a {\r\n  color: black;\r\n  padding: 12px 16px;\r\n  text-decoration: none;\r\n  display: block;\r\n}\r\n/* Change color of dropdown links on hover */\r\n.dropdown-content a:hover {background-color: #ddd;}\r\n/* Show the dropdown menu on hover */\r\n.dropdown:hover .dropdown-content {display: block;}\r\n.dropdown:hover .dropbtn {background-color: #3e8e41;}\r\n/************ END HOVER MOUSE *******************/\r\n/******* CUSTOM TOGGLE SWITCH *******/\r\n.switch input { \r\n  display:none;\r\n}\r\n.switch {\r\n  display:inline-block;\r\n  width:55px;\r\n  height:25px;\r\n  margin:8px;\r\n  -webkit-transform:translateY(50%);\r\n          transform:translateY(50%);\r\n  position:relative;\r\n}\r\n.slider {\r\n  position:absolute;\r\n  top:0;\r\n  bottom:0;\r\n  left:0;\r\n  right:0;\r\n  border-radius:30px;\r\n  box-shadow:0 0 0 2px #777, 0 0 4px #777;\r\n  cursor:pointer;\r\n  border:4px solid transparent;\r\n  overflow:hidden;\r\n   transition:.4s;\r\n}\r\n.slider:before {\r\n  position:absolute;\r\n  content:\"\";\r\n  width:100%;\r\n  height:100%;\r\n  background:#777;\r\n  border-radius:30px;\r\n  -webkit-transform:translateX(-30px);\r\n          transform:translateX(-30px);\r\n  transition:.4s;\r\n}\r\ninput:checked + .slider:before {\r\n  -webkit-transform:translateX(30px);\r\n          transform:translateX(30px);\r\n  background:green;\r\n}\r\ninput:checked + .slider {\r\n  box-shadow:0 0 0 2px green,0 0 2px green;\r\n}\r\n/*********** END TOGGLE SWITCH **********/\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"