import { Profile } from './profile.model';
import { Article, User } from 'src/app/Core';

export class Comment {
  id: number;
  body: String;
  createdAt: string;
  author: string;

  
constructor(article: Article, body: String, userid: string) {
  this.id = article.ArticleID;
  this.body = body;
  this.createdAt = "";
  this.author = userid;



}
}